* {
font-family: Helvetica ,Arial, sans-serif;
}

body {
  padding: 60px;
  text-align: center;
}

input {
  color: gray;
  font-size: 1.2rem;
  width: 100%;
}

.card {
  background-color: white;
  border-radius: 0.25rem;
  box-shadow: 0 20px 40px -14px rgba(0, 0, 0, 0.25);
  display: flex;
  flex-direction: column;
  overflow: hidden;
  margin-top: 20px;
  max-width: 300px;
}

.card p {
  padding: 5px;
  text-align: center;
}

.card img {
  max-width: 100%;
}

.card-grid {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  list-style: none;
  margin: 0;
  padding: 0;
  justify-content: center;
}